.Article {
    display: flex;
    flex-direction: column;
    background-color: #282c34;
    min-height: 100vh;
    color: white;
    max-width: 42em;
    margin: 0 auto;
    padding: 2em 2em;
}

.Article-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: baseline;
    padding-top: 3em;
    padding-bottom: 1em;
}

.Article-title {
    color: #fff;
    font-weight: bolder;
    font-size: 2.2em;
    font-family: 'Montserrat';
    margin-bottom: .2em;
}

.Article-brand {
    font-weight: bolder;
    font-size: 1.8em;
    font-family: 'Montserrat';
    margin-bottom: 2em;
}

.Article-brand-link {
    color: #ffa7c4;
}

.Article-body {
    margin-bottom: 2em;
}

.Article-comments {
    font-size: 1.8em;
    margin-bottom: .8em;
    cursor: pointer;
}

.Article-comments-box {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.Article-comments-textarea {
    width: 100%;
    min-height: 6em;
    background-color: #282c34;
    color: #efefef;
    resize: none;
    margin-bottom: 1.2em;
    padding: .8em;
    overflow: hidden;
}

.Article-comments-send {
    padding: .8em 1em;
    border-radius: 2px;
    border: none;
    background-color: #ffa7c4;
    color: #282c34;
    font-weight: bolder;
    cursor: pointer;
}

.Article-comments-send:hover {
    background-color: #ff6b9c;
    color: #f6f6f6;
}