.Navbar-nav {
    display: flex;
    padding: 1em 3em;
    color: #f3f3f3
}

.Navbar-left {
    display: flex;
    justify-content: flex-start;
}

.Navbar-right {
    display: flex;
    justify-content: flex-end;
    width:100%;
}

.Navbar-brand {
    cursor: pointer;
    font-size: 2em;
    font-weight: bold;
    font-family: 'Montserrat';
    color: white;
}

.Navbar-login {
    cursor: pointer;
    color: white;
}

.Navbar-register {
    cursor: pointer;
    color: white;
    margin-left: .8em;  
}

.Navbar-logout {
    cursor: pointer;
    color: white;
}