.Login-container {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    color: white;
}

.Login-form {
    display: flex;
    flex-direction: column;
}

.Login-password {
    margin-bottom: 1.2em;
}

.Login-submit-container {
    display: flex;
    justify-content: flex-end;
}

.Login-submit {
    padding: .5em 1em;
    border-radius: 2px;
    border: none;
    background-color: #ffa7c4;
    color: #282c34;
    font-weight: bolder;
    cursor: pointer;
}

.Login-submit:hover {
    background-color: #ff6b9c;
    color: #f6f6f6;
}